Office.onReady(() => {
  console.log("✅ Office.js ready in functions.js");

  const apiBase = "https://env-muslimxchange-staging.kinsta.cloud/wp-json/mx/v1";

  /**
   * Helper to call the API
   * @param {string} endpoint 'ticker' or 'isin'
   * @param {string} identifier Value of ticker or ISIN
   * @param {string[]} fields Array of column names
   * @returns {Promise<string[][]>}
   */
  async function fetchData(endpoint, identifier, fields) {
    try {
      const token = await OfficeRuntime.storage.getItem("jwt");
      const headers = token ? { Authorization: `Bearer ${token}` } : {};

      const params = new URLSearchParams({
        [endpoint]: identifier,
        fields: fields.join(",")
      });

      const response = await fetch(`${apiBase}/${endpoint}?${params.toString()}`, { headers });
      const data = await response.json();

      if (data.error) {
        return [[data.error]];
      }

      const row = fields.map(field => data[field] ?? "N/A");
      return [row];
    } catch (err) {
      console.error("❌ Request failed:", err);
      return [["❌ Request Failed"]];
    }
  }

  /**
   * Excel custom function to fetch compliance fields by Ticker.
   * @customfunction
   * @param {string} ticker The stock ticker symbol
   * @param {string[]} fields Array of fields to retrieve
   * @returns {Promise<string[][]>} Array of values as a horizontal row
   */
  async function MXTICKER(ticker, fields) {
    return fetchData("ticker", ticker, fields);
  }

  /**
   * Excel custom function to fetch compliance fields by ISIN.
   * @customfunction
   * @param {string} isin The ISIN code
   * @param {string[]} fields Array of fields to retrieve
   * @returns {Promise<string[][]>} Array of values as a horizontal row
   */
  async function MXISIN(isin, fields) {
    return fetchData("isin", isin, fields);
  }

  CustomFunctions.associate("MXTICKER", MXTICKER);
  CustomFunctions.associate("MXISIN", MXISIN);
});
